const projects = ['netflix_login', 'music_landing', 'book_cover', 'menu_icon'];

const composeItem = title => {
  const item = document.createElement('li');
  const link = document.createElement('a');

  link.href = `./${title}/${title}.html`;
  link.textContent = title;
  item.appendChild(link);

  return item;
};

const composeList = titles => {
  const list = document.createElement('ul');

  titles.forEach(title => list.appendChild(composeItem(title)));

  return list;
};

document.onload = document.body.appendChild(composeList(projects));
